<template>
  <!--我的工作台组件-->
  <div :class="{'search-open-contact':!$store.state.headerStatus}">
    <!-- <div>
      <van-cell is-link @click="showPopup">展示弹出层</van-cell>
      <message :isShow="isShow"></message>
    </div>
    <div>
      <input type="button" value="显示弹窗" @click="showToast" />
    </div>-->
    <!-- <div>时间格式：{{time | fmtDate('hh:mm:ss')}}</div> -->
    <template v-for="group in workbenchList">
      <!--首字母-->
      <div :ref="`key_${group.groupName}`" class="weui-cells__title">
        <span class="Square"></span>
        {{group.groupTitle}}
      </div>
      <div class="weui-cells">
        <router-link
          :key="index"
          :to="{path:'/workbench/app',query:{appId:app.title}}"
          class="weui-cell weui-cell_access workbench-cell"
          v-for="(app,index) in group.apps"
          tag="div"
        >
          <div class="workbench-cell-inner">
            <div
              class="weui-cell__hd"
              :class="[(app.style=='blue')? 'blue' : '',(app.style=='red')? 'red' : '',(app.style=='yellow')? 'yellow' : '',(app.style=='green')? 'green' : '']"
            >
              <img
                :src="require('../../assets/images/workbench/'+app.ico)"
                class="home__mini-avatar___1nSrW"
              />
              <div class="message" v-show="app.unread>0">{{app.unread}}</div>
            </div>
            <div class="weui-cell__bd" style="font-size:12px;">
              <!-- {{item.remark?item.remark:item.nickname}} -->
              {{app.title}}
            </div>
          </div>
        </router-link>
      </div>
    </template>
  </div>
</template>
<script>
import message from "../../components/common/message";
import toast from "../../toast/toast";
export default {
  components: {
    message,
    toast
  },
  data() {
    return {
      isShow: false,
      baseUrl: "../../../assets/images/workbench/",
      timer: null,
      workbenchList: []
    };
  },
  mounted() {
    this.onLoad();
    // this.timedRefresh();
    //this.$toast("我是弹出消息");
  },
  methods: {
    showToast() {
      this.$toast("我是弹出消息");
    },
    test() {
      setTimeout(() => {
        this.isShow = false;
      }, 4000);
    },
    showPopup() {
      this.isShow = true;
      this.test();
    },
    onLoad() {
      this.$http.get("/api/app/appList").then(res => {
        this.workbenchList = res.data.data.groups;
        //console.log(res.data.data);
        this.$store.state.newWorkCount = res.data.data.total;
        //console.log(this.$store.state.newWorkCount);
      });
    },
    timedRefresh() {
      this.timer = setInterval(() => {
        this.onLoad();
      }, 15000);
    }
  },
  computed: {
    // workbenchList() {
    //   //console.log(this.$store.getters.workbenchList);
    //   return this.$store.getters.workbenchList;
    // }
  }
  // destroyed(){
  //       clearInterval(this.timer);
  // }
};
</script>
<style>
.workbench-cell {
  display: inline-block;
  width: 25%;
}
.workbench-cell-inner {
  width: 52px;
  margin: 0 auto;
  text-align: center;
}
.workbench-cell-inner .weui-cell__hd {
  margin-right: 0px;
  border-radius: 6px;
  padding: 8px 0;
  margin-bottom: 10px;
  position: relative;
}
.workbench-cell-inner .weui-cell__hd img {
  width: 30px;
  height: 30px;
}
.message {
  width: 18px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  color: white;
  background: #f43531;
  border-radius: 50%;
  position: absolute;
  top: -5px;
  right: -5px;
}
/* 小方块样式 */
.Square {
  display: inline-block;
  width: 8px;
  height: 8px;
  background: #3e5e77;
  vertical-align: top;
  margin-top: 8px;
}
.green {
  background: #4d897d;
}
.red {
  background: #b26b7d;
}
.blue {
  background: #416885;
}
.yellow {
  background: #f3b369;
}
</style>



